<template>
	<div class="citylist" ref="ratings">
		<div class="area">
			<div class="title border-topbottom"></div>
			<div class="buttonList">
				<div class="buttonWrap">
					<div class="button"></div>
				</div>
			</div>
		</div>
		<div class="area">
			<div class="title border-topbottom"></div>
			<div class="buttonList">
				<div class="buttonWrap" v-for="item in citylist.hotCities">
					<div class="button">{{item.name}}</div>
				</div>
			</div>
		</div>
		<div class="area" v-for="(item,index) in citylist.cities">
			<div class="title border-topbottom">{{index}}</div>
			<div class="itemlist">
				<div class="item border-bottom" v-for="inneritem in item">
					{{inneritem.name}}
				</div>
			</div>
		</div>
		<div class="alphabet">
			<ul>
				<li v-for="(item, index) in citylist.cities">{{index}}</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import BScroll from 'better-scroll';
	export default{
		props:{
			citylist:{
				type: Object
			}
		},
		created(){
		 this.$nextTick(() =>{
				if (!this.scroll) {
					this.scroll = new BScroll(this.$refs.ratings, {
						click: true
					})
				}else{
					this.scroll.refresh();
				}
			});
		},
	}
</script>

<style lang="scss">
	.citylist{
		position: absolute;
		top: 1.58rem;
		left: 0;
		right: 0;
		bottom:0;
		overflow: hidden;
		.title{
			line-height: .54rem;
			background: #eee;
			padding-left: .2rem;
			color: #666;
			font-size: .26rem;
		}
		.border-topbottom{
			&:before{
				border-color: #ccc;
			}
			&:after{
				border-color: #ccc;
			}
		}
		.buttonList{
			overflow: hidden;
			padding:.1rem .6rem .1rem .1rem;
			.buttonWrap{
				width: 33.33%;
				float: left;
				.button{
					text-align: center;
					margin: .1rem;
					padding:.1rem 0;
					border:.02rem solid #ccc;
					border-radius:.06rem;
				}
			}
		}
		.itemlist{
			.item{
				line-height: .76rem;
				color: #666;
				padding-left:.2rem;

			}
			.border-bottom{
				&:before{
					border-color: #ccc;
				}
			}
		}
		.alphabet{
			display: flex;
			flex-direction: column;
			justify-content: center;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: .4rem;
			ul{
				li{
					text-align: center;
					line-height: .4rem;
					color:#00bcd4;
				}
			}
		}
	}
</style>