<template>
	<div class="city">
		<cheader></cheader>
		<search></search>
		<citylist :citylist = "citylist"></citylist>
	</div>
</template>

<script>
	import axios from 'axios';
	import cheader from '../components/city/header/header.vue'
	import search from '../components/city/search/search.vue'
	import citylist from '../components/city/citylist/citylist.vue'
	
	export default{
		data() {
			return{
				citylist:{}
			}
		},
		components:{
			cheader,
			search,
			citylist,
		},
		mounted(){
			axios.get('./city.json').then((res) =>{
				this.citylist = (res.data.data)
			})
		}
	}
</script>

<style lang="scss">
	.city{
		
	}
</style>